import {getStyle} from '../../config/mUtils'
export const loadMore={
    directives: {
        'loadMore':{
            bind:(el,binding) => {
                
                let widonWHeight=window.screen.height;//浏览器平路高度
                let height;//页面高度
                let setTop;//底部距离顶部的高度
                let paddingButton;
                let marginButton;
                let requestFrom;//动画
                let oldScrollTop;
                let scrollEl;
                let heightEl;
                let scrollType=el.attributes.type && el.attributes.type.value;//判断type时纯在传过来的
                let scrollReduce=2;
                //传入2是相当于上一级滑动不是2就是相当于body滑动
                if(scrollType == 2)
                {
                    scrollEl=el;
                    heightEl=el.children[0];
                }else{
                    scrollEl=document.body;
                    heightEl=el;
                }
                //当手指触摸的时候
                el.addEventListener('touchstart',() =>{
                    console.log("触摸")
                    height=heightEl.clientHeight;//获取当前DOM可见的高度
                    if(scrollType==2)
                    {
                        height=height;
                    }
                    setTop=el.offsetTop;//当前DOM距离上一个对象的距离
                    paddingButton=getStyle(el,'paddingBottom');
                    marginButton=getStyle(el,'marginBottom');
                },false);
                //手指再屏幕上滑动触发
                el.addEventListener('touchmove',()=>{
                    loadMore();
                },false);
                //手指离开的时候触发
                el.addEventListener('touchend',()=>{
                    oldScrollTop=scrollEl.scrollTop;//当钱对象的滚动条的距离
                    moveEnd();
                },false);
                const moveEnd=()=>{
                    //window.requestAnimationFrame() 
					//方法告诉浏览器您希望执行动画并请求浏览器在下一次重绘之前调用指定的函数来更新动画。
                    //该方法使用一个回调函数作为参数，这个回调函数会在浏览器重绘之前调用。
                    requestFrom=requestAnimationFrame(()=>{
                        if(scrollEl.scrollTop!=oldScrollTop)
                        {
                            oldScrollTop=scrollEl.scrollTop;
                            moveEnd();
                        }else{
                            cancelAnimationFrame(requestFrom);//取消动画函数
                            //防止当手指离开屏幕后刚好刷新的话就要从新获取DOM高度然后再执行判断函数
                            height=heightEl.clientHeight; 
                            loadMore();              
                        }
                    })
                }
                const loadMore=()=>{
                    //最终计算
                    //console.log(marginButton);
                    //console.log(scrollEl.scrollTop+widonWHeight+"----"+(height+setTop));
                     //滚动条高度+浏览器高度>=当前DOM高度+上部div高度+外边距底部边距+内边距底部高度-2
                    if(scrollEl.scrollTop+widonWHeight >= height+setTop+marginButton+paddingButton-scrollReduce)
                    {
                       
                        binding.value();
                    }
                }
            }
        }
    }
}
//处理图片地址
export const getImgPath={
    methods:{
        getImgPath(path)
        {
            let suffix;
            if(!path)
            {
                return 'http://test.fe.ptdev.cn/elm/elmlogo.jpeg';
            }else if(path.indexOf('jpeg') !==-1)
            {
                suffix='.jpeg';
            }else{
                suffix='.png';
            }
        
         let url='/'+path.substr(0,1)+'/'+path.substr(1,2)+'/'+path.substr(3)+suffix;
         return 'https://fuss10.elemecdn.com'+url;
        }
    }
}


