<template>
  <div class="alet_container">
      <section class="tip_text_container">
          <div class="tip_icon">
              <span></span>
              <span></span>
          </div>
          <p class="tip_text">{{alertText}}</p>
          <div class="confirim" @click="closeTip">确认</div>
      </section>
  </div>
</template>

<script>
export default {
    data()
    {
        return{
            positionY:0,
            timer:null
        }
    },methods:{
        closeTip()
        {
            //this.$emit触发父组件的事件
            this.$emit('closeTip')
        }
    },
    props:['alertText']
}
</script>

<style scoped lang="scss">
@import '../../style/mixin.scss';
@keyframes tipMove {
    // scale缩放
    0%{transform: scale(1)}
    35%{transform: scale(.8)}
    70%{transform: scale(1.1)}
    100%{transform: scale(1)}
}
.alet_container{
    position: fixed;//生成绝对定位的元素，相对于浏览器窗口进行定位遮掩
    top: 0;
    left:0;
    right: 0;
    bottom: 0;
    z-index: 200;
}

.tip_text_container{
    position: absolute;
    top:50%;
    left:50%;
    margin-top:-6rem;
    margin-left:-6rem;
    width: 12rem;
    animation: tipMove .4s;
    background-color: rgba(255,255,255,1);
    border:1px;
    padding-top: .6rem;
    display: flex;
    justify-content: center;//弹性项目居中紧挨着填充
    align-items: center;//弹性盒子元素在该行的侧轴（纵轴）上居中放置
    flex-direction: column;//以纵向排列
    border:1px;
    border-radius: .25rem;
    .tip_icon{
        @include wh(3rem,3rem);
        border:0.15rem solid #f8cb86;
        justify-content: center;
        display: flex;
        flex-direction: column;
        align-items: center;
        border-radius: 50%;
        
         span:nth-of-type(1)
        {
            @include wh(.12rem,1.5rem);
            
            background-color: #f8cb86;
        }
        span:nth-of-type(2)
        {
            @include wh(.2rem,.2rem);
            border:1px;
            border-radius: 50%;
            margin-top: .2rem;
            background-color: #f8cb86;
        }
    }
    .tip_text{
        @include sc(.7rem,#333);
        line-height: .9rem;
        margin-top: .8rem;
        text-align: center;
        padding:0.4rem;
    }
    .confirim{
        @include sc(.8rem,#fff);
        font-weight: bold;
        margin-top: .8rem;
        background-color: #4cd964;
        width: 100%;
        text-align: center;
        line-height: 1.8rem;
        border:1px;
        border-bottom-left-radius: 0.25rem;
        border-bottom-right-radius: 0.25rem;
    }
}
</style>



